import { Component, Prop, h, Host } from '@stencil/core';

export type IllustrationSize = 'sm' | 'md' | 'lg' | 'xl' | '2xl' | '3xl' | '4xl';

@Component({
  tag: 'bh-illustrations',
  styleUrl: 'bh-illustrations.css',
  shadow: true,
})
export class BhIllustrations {
  /**
   * Size of the illustration
   */
  @Prop() size: IllustrationSize = 'lg';

  /**
   * Title text to display below the illustration
   */
  @Prop() illustrationTitle?: string;

  /**
   * Description text to display below the title
   */
  @Prop() description?: string;

  /**
   * Path to the SVG illustration
   */
  @Prop() svgPath: string = '/illustrations/Search-not-found.svg';

  /**
   * Alt text for the illustration image
   */
  @Prop() alt: string = 'Illustration';

  private getSvgDimensions(): { width: number; height: number } {
    switch (this.size) {
      case 'sm':
        return { width: 120, height: 120 };
      case 'md':
        return { width: 160, height: 160 };
      case 'lg':
        return { width: 200, height: 200 };
      case 'xl':
        return { width: 280, height: 280 };
      case '2xl':
        return { width: 360, height: 360 };
      case '3xl':
        return { width: 480, height: 480 };
      case '4xl':
        return { width: 600, height: 600 };
      default:
        return { width: 200, height: 200 };
    }
  }

  render() {
    const dimensions = this.getSvgDimensions();

    return (
      <Host>
        <div class={`illustration illustration-${this.size}`}>
          <div class="illustration-wrapper">
            <div
              class="illustration-svg-container"
              style={{
                width: `${dimensions.width}px`,
                height: `${dimensions.height}px`,
              }}
            >
              <img
                src={this.svgPath}
                alt={this.alt}
                style={{
                  width: `${dimensions.width}px`,
                  height: `${dimensions.height}px`,
                }}
                class="illustration-image"
              />
            </div>
            {(this.illustrationTitle || this.description) && (
              <div class="illustration-content">
                {this.illustrationTitle && (
                  <h3 class="illustration-title">{this.illustrationTitle}</h3>
                )}
                {this.description && (
                  <p class="illustration-description">{this.description}</p>
                )}
              </div>
            )}
          </div>
        </div>
      </Host>
    );
  }
}
